'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import api from '@/lib/api';
import Navigation from '@/components/Navigation';
import Link from 'next/link';
import { ShoppingBag, Package, CreditCard, TrendingUp } from 'lucide-react';

export default function DashboardPage() {
  const { user, isAuthenticated, loading: authLoading } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState({
    totalOrders: 0,
    totalSpent: 0,
    cartItems: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!authLoading && !isAuthenticated) {
      router.push('/login');
      return;
    }
    if (isAuthenticated) {
      fetchStats();
    }
  }, [isAuthenticated, authLoading]);

  const fetchStats = async () => {
    try {
      // Get orders
      const ordersResponse = await api.get('/orders');
      const orders = ordersResponse.data.orders || [];
      
      const totalSpent = orders.reduce((sum: number, order: any) => sum + order.total, 0);

      // Get cart
      const cartResponse = await api.get('/cart');
      const cartItems = cartResponse.data.cart?.itemCount || 0;

      setStats({
        totalOrders: orders.length,
        totalSpent,
        cartItems,
      });
    } catch (error) {
      console.error('Error fetching stats:', error);
    } finally {
      setLoading(false);
    }
  };

  if (authLoading || loading) {
    return (
      <div className="min-h-screen bg-gray-50">
        <Navigation />
        <div className="container mx-auto px-4 py-20 text-center">Loading...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <Navigation />
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-4xl font-bold mb-2">
          Welcome back, {user?.name}! ⭐
        </h1>
        <p className="text-gray-600 mb-8">Here's your ShopStar dashboard</p>

        {/* Stats Cards */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white p-6 rounded-lg shadow-md">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-gray-600 text-sm">Total Orders</p>
                <p className="text-3xl font-bold mt-2">{stats.totalOrders}</p>
              </div>
              <Package className="w-12 h-12 text-purple-600" />
            </div>
            <Link href="/orders" className="text-purple-600 hover:underline text-sm mt-4 block">
              View all orders →
            </Link>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-md">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-gray-600 text-sm">Total Spent</p>
                <p className="text-3xl font-bold mt-2">${stats.totalSpent.toFixed(2)}</p>
              </div>
              <TrendingUp className="w-12 h-12 text-green-600" />
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-md">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-gray-600 text-sm">Cart Items</p>
                <p className="text-3xl font-bold mt-2">{stats.cartItems}</p>
              </div>
              <ShoppingBag className="w-12 h-12 text-blue-600" />
            </div>
            <Link href="/cart" className="text-purple-600 hover:underline text-sm mt-4 block">
              View cart →
            </Link>
          </div>
        </div>

        {/* Quick Actions */}
        <div className="grid md:grid-cols-2 gap-6">
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h2 className="text-2xl font-bold mb-4">Quick Actions</h2>
            <div className="space-y-3">
              <Link
                href="/products"
                className="block w-full bg-purple-600 text-white text-center py-3 rounded-lg font-semibold hover:bg-purple-700"
              >
                Browse Products
              </Link>
              <Link
                href="/cart"
                className="block w-full bg-white text-purple-600 border-2 border-purple-600 text-center py-3 rounded-lg font-semibold hover:bg-purple-50"
              >
                View Cart
              </Link>
              <Link
                href="/orders"
                className="block w-full bg-white text-purple-600 border-2 border-purple-600 text-center py-3 rounded-lg font-semibold hover:bg-purple-50"
              >
                My Orders
              </Link>
            </div>
          </div>

          <div className="bg-white p-6 rounded-lg shadow-md">
            <h2 className="text-2xl font-bold mb-4">Account Info</h2>
            <div className="space-y-2">
              <p><span className="font-semibold">Name:</span> {user?.name}</p>
              <p><span className="font-semibold">Email:</span> {user?.email}</p>
              <p><span className="font-semibold">Role:</span> <span className="capitalize">{user?.role}</span></p>
              {user?.role === 'admin' && (
                <Link
                  href="/admin"
                  className="inline-block mt-4 bg-purple-600 text-white px-4 py-2 rounded-lg font-semibold hover:bg-purple-700"
                >
                  Admin Dashboard
                </Link>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

